
import { defineComponent } from 'vue';

import Header from '@/components/Header.vue';
import Footer from '@/components/Footer.vue';

export default defineComponent({
  name: 'Guide',
  components: {
    Header,
    Footer
  },
  data() {
    return {
      syllabus: []
    };
  },
  async created() {
    this.syllabus = await this.axios.get('/apis/syllabus.json'); 
  }
});
